import {
	PanelBody,
	SelectControl,
	ServerSideRender,
} from '@wordpress/components';
import  {
	useSelect,
} from '@wordpress/data'
import {
	InspectorControls,
	useBlockProps
} from '@wordpress/block-editor'
import { __ } from '@wordpress/i18n';

export default function Edit( { attributes, setAttributes } ) {
	const {
		productId,
	} = attributes;

    const blockProps = useBlockProps();

	const { products } = useSelect( (select) => { 
		
		let products = select( 'core' ).getEntityRecords( 'postType', 'product', {
			per_page: 100,
		} );

		if ( products ) {
			products = products.filter( item => item.meta['_iconic_wsb_product_page_order_bump_ids'] && item.meta['_iconic_wsb_product_page_order_bump_ids'].length > 0 && item.meta['_iconic_wsb_product_page_order_bump_ids'][0] && item.meta['_iconic_wsb_product_page_order_bump_ids'][0].length );
			products = products.map( item => {
				let sku = item.meta['_sku'];
				if ( ! sku || sku.length === 0 ) {
					sku = item.id.toString();
				}

				const productSelect = {
					value: item.id.toString(),
					label: `#${item.id} - ${item.title.raw} (${sku})`,
				};
				return productSelect;
			});
			products.sort(function(a, b) {
				if ( a.label < b.label ){
					return -1;
				}
				if ( a.label > b.label ){
					return 1;
				}
				return 0;
			});
			products.unshift( {
				value: '',
				label: __('Please Select...', 'iconic-wsb'),
			});
		}

		return {
			products,
		}
	});

	if ( ! products ) {
		return null;
	}

	return [
        <InspectorControls key="sidebar">
			<PanelBody
				initialOpen={ true }
				title={ __( 'Choose Product', 'iconic-wsb' ) }
			>
                <SelectControl
                    label={ __('Product', 'iconic-wsb') }
					help={ __( 'Please choose a product from the dropdown.', 'iconic-wsb' ) }
                    onChange={ ( productId ) => setAttributes( { productId } ) }
                    value={ productId }
                    options={products}
                />
			</PanelBody>
		</InspectorControls>,
        <div { ...blockProps } key="block">
			<SelectControl
				label={ __('Product', 'iconic-wsb') }
				help={ __( 'Please choose a product from the dropdown, a preview will be rendered below.', 'iconic-wsb' ) }
				onChange={ ( productId ) => setAttributes( { productId } ) }
				value={ productId }
				options={products}
			/>
			{ productId && (
				<ServerSideRender
					block='iconic-wsb/fbt'
					attributes={ attributes }
				/>
			) }
        </div>
    ];
}

